<template>
	<div class="pdawrap">
		<div class="search_out">
			<!-- 库位搜索 -->
			<div class="choose_div">
				<span class="searchout_title">{{$t('storage.category_name')}}</span>
				<template>
			  	<el-select value-key="id" v-model="warehouse_category_id" @change="categoryTypeChange">
			    	<el-option
				      	v-for="item in categoryTypeList"
				      	:key="item.id"
				      	:label="item.category_name"
				      	:value="item.id">										      
			    	</el-option>
					</el-select>
				</template>
			</div>
			<div class="choose_div">
				<span class="searchout_title">{{$t('storage.warehouse_name')}}</span>
				<template>
			  	<el-select value-key="id" v-model="warehouse_name_id" >
		    	<el-option
			      	v-for="item in warehouselist"
			      	:key="item.id"
			      	:label="item.warehouse_name"
			      	:value="item.id">										      
		    	</el-option>
				</el-select>
				</template>
			</div>
			<!-- 搜素条件 -->
			<div class="choose_div">
				<span class="searchout_title">{{$t('pda.searchtype')}}</span>
				<template>
			  	<el-select value-key="type_id" v-model="type" @change="typeChange(type)">
			    	<el-option
			    		change
			      	v-for="item in options"
			      	:key="item.type_id"
			      	:label="item.name"
			      	:value="item.type_id">
			    	</el-option>
			  	</el-select>
				</template>
			</div>
			<!-- 动态搜索 -->
			<div class="pda_selected_div">
				<span class="searchout_title" v-show="this.type=='steel_model'">{{$t('storage.steel_model')}}:</span>
				<span class="searchout_title" v-show="this.type=='order_code'">{{$t('storage.order_code')}}:</span>
				<span class="searchout_title" v-show="this.type=='version_num'">{{$t('storage.version_num')}}:</span>
				<span class="searchout_title" v-show="this.type=='supplier_name'">{{$t('storage.supplier_name')}}:</span>
				<span class="searchout_title" v-show="this.type=='label_code'">{{$t('storage.label_code')}}:</span>
				<el-input v-model="searchcontent"></el-input>
			</div>
					<div class="lights_search">
						<el-button type="primary" icon="el-icon-search" size="small" @click="search" :disabled="isDisabled?true:false">{{$t('common.search')}}</el-button>
					</div>
			<!-- 	</div>
			</div> -->
		</div>
		<!-- 数据表格 -->
		<div class="pda_table_div">
		
			<template>
				<el-table
					:header-cell-style="{background:'#DFEBF9',color:'#606266'}"
					highlight-current-row
    			@current-change="handleCurrent"
					:data="mate_list">
          <el-table-column
          	:label="$t('pda.num')"
			      type="index"
			      width="60">
			    </el-table-column>							
					<el-table-column
						prop="label_code"
						:show-overflow-tooltip='true'
					  :label="$t('storage.label_code')">						
					</el-table-column>
					<el-table-column
						prop="state"
					  	:label="$t('storage.state')"
					  	width="100">
						<template slot-scope="scope">
							<span v-if="scope.row.state==0" class="take_mat_span">在库</span>
							<span v-if="scope.row.state==1" class="stay_mat_span">待取</span>
						</template> 							
					</el-table-column>			
				</el-table>
			</template>
			
		</div>
	</div>
</template>
<script>
 import { mapMutations } from 'vuex'
 import { searchByInfo, outByPosition } from '@/request/api/material'
 import {searchWarehouseCategory, searchWarehouseNameByCategory } from '@/request/api/materialshelf'
	export default{
		data(){
			return{
				type:'part_num',
				warehouse_category_id:0,
				warehouse_name_id:0,
				searchcontent:'',
				isDisabled:false,
				options:[
					{name:this.$t('storage.part_num'),type_id:'part_num'},
					{name:this.$t('storage.description'),type_id:'description'},
					{name:this.$t('storage.shelf_id'),type_id:'shelf_id'},
				],
				categoryTypeList:[{'category_name':'所有','id':0}],
				warehouselist:[{'warehouse_name':'所有','id':0}],
				mate_list:[],
				expands: [],
        getRowKeys (row) {
          return row.id
        },
			}
		},
		mounted(){
			this.getCategoryType()
		},
		methods:{
			...mapMutations(['changeRow']),
			typeChange(type){
				this.type = type
				this.searchcontent = ''
			},

			search(){
				let _this = this
				this.isDisabled = true
				let data ={
					warehouse_category_id:this.warehouse_name_id,
					warehouse_name_id:this.warehouse_name_id
				}
				if ( this.type=='part_num') {
					data.part_num =this.searchcontent
				}
				if (this.type=='description') {
					data.description = this.searchcontent
				}
				if (this.type=='shelf_id') {
					data.shelf_id = this.searchcontent
				}
				searchByInfo(data).then(res =>{
					if (res.result) {
						if (res.mate_list.length ==0) {
							this.$message(this.$t('common.nodata'));
							this.mate_list= []
						} else {
							this.mate_list = res.mate_list;
						}
					} else {
						this.$message({
	          	showClose: true,
	          	message: res.msg,
	          	type: 'error'
	        	})
					}
				})
				let Timer=window.setTimeout(function(){
					_this.isDisabled = false
				},1500)
			},
				// 获取仓库类型
	    getCategoryType(){
	    	searchWarehouseCategory({}).then(res =>{
	    		if (res.result) {
	    			this.categoryTypeList = this.categoryTypeList.concat(res.data_list)
	    		}
	    	})
	    },
	    // 获取库位
	    getWarehouseList(){
	    	this.warehouselist = [{'warehouse_name':'所有','id':0}]
	    	this.warehouse_name_id = 0
	    	searchWarehouseNameByCategory({warehouse_category_id:this.warehouse_category_id}).then(res =>{
	    		if (res.result) {
	    			this.warehouselist = this.warehouselist.concat(res.data)
	    		}
	    	})
	    },
	    categoryTypeChange(){
	    	this.warehouse_name_id = 0
	    	this.getWarehouseList()
	    },
			handleCurrent(row){
				this.changeRow({ row : row})
				this.$router.push('/detail')
			},
		}
	}
</script>
<style >
 .search_out{
    font-size: 0.4rem;
  }
  .search_out .el-input__suffix{
    height: 1.06rem;
    line-height: 1.06rem;
  }
  .search_out .el-input__inner{
    width:100%;
    height: 1.06rem;
    font-size: 0.4rem;
  }
  .search_out .el-input{
    width:100%;
    height: 1.06rem;
    font-size: 0.4rem;
  }
</style>


